'use client';

import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Container, Section } from '../ui/Container';

const SkillGrid = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-top: 3rem;
`;

const SkillPill = styled(motion.div)`
  padding: 0.75rem 1.5rem;
  background: white;
  border: 1px solid var(--border);
  border-radius: 100px;
  font-weight: 600;
  color: var(--secondary);
  box-shadow: var(--shadows-sm);
  cursor: default;
  &:hover {
    color: var(--primary);
    border-color: var(--primary);
    box-shadow: var(--shadows-md);
  }
`;

const SectionTitle = styled.h2`
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1rem;
`;

const SectionDesc = styled.p`
  text-align: center;
  color: var(--secondary);
  max-width: 600px;
  margin: 0 auto;
`;

export const Skills = ({ data }: { data: any[] }) => {
    return (
        <Section style={{ background: 'var(--accent)' }}>
            <Container>
                <SectionTitle>Technical Skills</SectionTitle>
                <SectionDesc>Professional expertise in modern web technologies and design patterns.</SectionDesc>
                <SkillGrid>
                    {data?.map((skill, i) => (
                        <SkillPill
                            key={skill.name}
                            initial={{ opacity: 0, scale: 0.8 }}
                            whileInView={{ opacity: 1, scale: 1 }}
                            transition={{ delay: i * 0.05 }}
                            viewport={{ once: true }}
                            whileHover={{ y: -5 }}
                        >
                            {skill.name}
                        </SkillPill>
                    ))}
                </SkillGrid>
            </Container>
        </Section>
    );
};
