'use client';

import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronDown, ChevronUp, ExternalLink } from 'lucide-react';
import { Container, Section } from '../ui/Container';

const ExpCard = styled.div`
  background: white;
  border: 1px solid var(--border);
  border-radius: 1.5rem;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadows-sm);
`;

const ExpHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  cursor: pointer;
`;

const CompanyInfo = styled.div`
  h3 {
    font-size: 1.5rem;
    color: var(--primary);
  }
  h4 {
    font-size: 1.1rem;
    color: var(--foreground);
    margin: 0.25rem 0;
  }
  span {
    color: var(--secondary);
    font-size: 0.9rem;
  }
`;

const CollapsibleContent = styled(motion.div)`
  overflow: hidden;
  margin-top: 1.5rem;
  border-top: 1px solid var(--border);
  padding-top: 1.5rem;
`;

const ProductGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
`;

const ProductCard = styled.div`
  border-radius: 1rem;
  overflow: hidden;
  border: 1px solid var(--border);
  img {
    width: 100%;
    height: 150px;
    object-fit: cover;
  }
  padding-bottom: 1rem;
  h5 {
    padding: 1rem 1rem 0.25rem;
  }
  p {
    padding: 0 1rem;
    font-size: 0.85rem;
    color: var(--secondary);
  }
`;

const AchievementList = styled.ul`
  list-style: disc;
  padding-left: 1.5rem;
  margin-bottom: 2rem;
  li {
    margin-bottom: 0.5rem;
    color: var(--secondary);
  }
`;

const ExperienceItem = ({ exp }: { exp: any }) => {
    const [isExpanded, setIsExpanded] = useState(false);

    return (
        <ExpCard>
            <ExpHeader onClick={() => setIsExpanded(!isExpanded)}>
                <CompanyInfo>
                    <h3>{exp.company}</h3>
                    <h4>{exp.role}</h4>
                    <span>{exp.period}</span>
                </CompanyInfo>
                <div>
                    {isExpanded ? <ChevronUp size={24} /> : <ChevronDown size={24} />}
                </div>
            </ExpHeader>

            <AnimatePresence>
                {isExpanded && (
                    <CollapsibleContent
                        initial={{ height: 0, opacity: 0 }}
                        animate={{ height: 'auto', opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                    >
                        <p style={{ marginBottom: '1rem' }}>{exp.description}</p>
                        <h5 style={{ marginBottom: '0.5rem' }}>Key Achievements:</h5>
                        <AchievementList>
                            {exp.achievements.map((a: string, i: number) => (
                                <li key={i}>{a}</li>
                            ))}
                        </AchievementList>

                        {exp.products && (
                            <>
                                <h5 style={{ marginBottom: '1rem' }}>Delivered Products:</h5>
                                <ProductGrid>
                                    {exp.products.map((p: any, i: number) => (
                                        <ProductCard key={i}>
                                            <img src={p.image} alt={p.name} />
                                            <h5>{p.name}</h5>
                                            <p>{p.description}</p>
                                        </ProductCard>
                                    ))}
                                </ProductGrid>
                            </>
                        )}
                    </CollapsibleContent>
                )}
            </AnimatePresence>
        </ExpCard>
    );
};

export const Experience = ({ data }: { data: any[] }) => {
    return (
        <Section>
            <Container>
                <h2 style={{ textAlign: 'center', fontSize: '2.5rem', marginBottom: '3rem' }}>Work Experience</h2>
                {data?.map((exp, i) => (
                    <ExperienceItem key={i} exp={exp} />
                ))}
            </Container>
        </Section>
    );
};
