'use client';

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { Menu, X } from 'lucide-react';
import { Container } from '../ui/Container';
import Link from 'next/link';

const Nav = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  z-index: 1000;
  border-bottom: 1px solid var(--border);
`;

const NavContent = styled(Container)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
`;

const Logo = styled(Link)`
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--primary);
  text-transform: uppercase;
  letter-spacing: 2px;
`;

const NavLinks = styled.div`
  display: flex;
  gap: 2rem;
  @media (max-width: 768px) {
    display: none;
  }
`;

const NavLink = styled(Link)`
  font-weight: 500;
  color: var(--secondary);
  transition: color 0.3s ease;
  &:hover {
    color: var(--primary);
  }
`;

const MenuButton = styled.button`
  display: none;
  @media (max-width: 768px) {
    display: block;
    color: var(--foreground);
  }
`;

const MobileDrawer = styled(motion.div)`
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 300px;
  background: white;
  z-index: 1001;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  box-shadow: -10px 0 30px rgba(0, 0, 0, 0.05);
`;

const Overlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 1000;
`;

export const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);

    return (
        <>
            <Nav>
                <NavContent>
                    <Logo href="/">SK.</Logo>
                    <NavLinks>
                        <NavLink href="/">Home</NavLink>
                        <NavLink href="/projects">Projects</NavLink>
                        <NavLink href="/contact">Contact Me</NavLink>
                        <NavLink href="/admin">Admin</NavLink>
                    </NavLinks>
                    <MenuButton onClick={toggle}>
                        {isOpen ? <X size={24} /> : <Menu size={24} />}
                    </MenuButton>
                </NavContent>
            </Nav>

            <AnimatePresence>
                {isOpen && (
                    <>
                        <Overlay
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            onClick={toggle}
                        />
                        <MobileDrawer
                            initial={{ x: '100%' }}
                            animate={{ x: 0 }}
                            exit={{ x: '100%' }}
                            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
                        >
                            <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                                <button onClick={toggle}><X size={24} /></button>
                            </div>
                            <NavLink href="/" onClick={toggle}>Home</NavLink>
                            <NavLink href="/projects" onClick={toggle}>Projects</NavLink>
                            <NavLink href="/contact" onClick={toggle}>Contact Me</NavLink>
                            <NavLink href="/admin" onClick={toggle}>Admin</NavLink>
                        </MobileDrawer>
                    </>
                )}
            </AnimatePresence>
        </>
    );
};
