'use client';

import styled from 'styled-components';
import { Container } from '../ui/Container';
import Link from 'next/link';

const FooterBase = styled.footer`
  padding: 5rem 0 2rem;
  border-top: 1px solid var(--border);
  background: var(--accent);
`;

const FooterGrid = styled.div`
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 4rem;
  margin-bottom: 4rem;
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
`;

const FooterCols = styled.div`
  h4 {
    margin-bottom: 1.5rem;
    font-size: 1.2rem;
  }
  ul {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  a {
    color: var(--secondary);
    transition: color 0.3s;
    &:hover {
      color: var(--primary);
    }
  }
`;

const Copyright = styled.div`
  text-align: center;
  padding-top: 2rem;
  border-top: 1px solid var(--border);
  color: var(--secondary);
  font-size: 0.9rem;
`;

export const Footer = () => {
    return (
        <FooterBase>
            <Container>
                <FooterGrid>
                    <FooterCols>
                        <h4 style={{ color: 'var(--primary)', fontWeight: 800 }}>SALEEM KUDRA</h4>
                        <p style={{ color: 'var(--secondary)', maxWidth: '300px' }}>
                            Building modern, scalable, and high-performance applications with 8 years of expertise.
                        </p>
                    </FooterCols>
                    <FooterCols>
                        <h4>Sitemap</h4>
                        <ul>
                            <li><Link href="/">Home</Link></li>
                            <li><Link href="/projects">Projects</Link></li>
                            <li><Link href="/contact">Contact</Link></li>
                            <li><Link href="/resume">Resume</Link></li>
                        </ul>
                    </FooterCols>
                    <FooterCols>
                        <h4>Connect</h4>
                        <ul>
                            <li><a href="#">LinkedIn</a></li>
                            <li><a href="#">GitHub</a></li>
                            <li><a href="#">Twitter</a></li>
                            <li><a href="#">Email</a></li>
                        </ul>
                    </FooterCols>
                </FooterGrid>
                <Copyright>
                    © {new Date().getFullYear()} Saleem Kudra. All rights reserved.
                </Copyright>
            </Container>
        </FooterBase>
    );
};
