'use client';

import styled from 'styled-components';
import { motion } from 'framer-motion';
import { Container, Section } from '../ui/Container';

const StrengthGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;

const ProgressBarWrapper = styled.div`
  margin-bottom: 2rem;
`;

const LabelWrap = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-weight: 600;
`;

const BarBase = styled.div`
  height: 10px;
  background: var(--border);
  border-radius: 5px;
  overflow: hidden;
`;

const BarFill = styled(motion.div)`
  height: 100%;
  background: var(--primary);
  border-radius: 5px;
`;

export const Strength = ({ data }: { data: any[] }) => {
    return (
        <Section style={{ background: 'var(--accent)' }}>
            <Container>
                <h2 style={{ textAlign: 'center', fontSize: '2.5rem', marginBottom: '3rem' }}>Core Strengths</h2>
                <StrengthGrid>
                    {data?.map((item, i) => (
                        <ProgressBarWrapper key={i}>
                            <LabelWrap>
                                <span>{item.name}</span>
                                <span>{item.level}%</span>
                            </LabelWrap>
                            <BarBase>
                                <BarFill
                                    initial={{ width: 0 }}
                                    whileInView={{ width: `${item.level}%` }}
                                    transition={{ duration: 1, delay: i * 0.1 }}
                                    viewport={{ once: true }}
                                />
                            </BarBase>
                        </ProgressBarWrapper>
                    ))}
                </StrengthGrid>
            </Container>
        </Section>
    );
};
