'use client';

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { Container, Section } from '../ui/Container';

const HeroGrid = styled.div`
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 4rem;
  align-items: center;
  min-height: 80vh;
  @media (max-width: 968px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
`;

const Left = styled.div``;

const Right = styled.div`
  position: relative;
  height: 400px;
  background: var(--accent);
  border-radius: 2rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadows-lg);
`;

const Title = styled(motion.h1)`
  font-size: 4rem;
  margin-bottom: 1.5rem;
  color: var(--foreground);
  @media (max-width: 576px) {
    font-size: 2.5rem;
  }
`;

const Subtitle = styled(motion.p)`
  font-size: 1.25rem;
  color: var(--secondary);
  line-height: 1.8;
  margin-bottom: 2rem;
`;

const SliderContent = styled(motion.div)`
  padding: 2rem;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  h3 {
    font-size: 1.5rem;
    color: var(--primary);
    margin-bottom: 1rem;
  }
`;

const highlights = [
    { title: "8+ Years Experience", text: "In Fullstack Ecosystem" },
    { title: "50+ Products", text: "Delivered to Enterprise Clients" },
    { title: "Top Performance", text: "Scalable & Robust Solutions" }
];

export const Hero = ({ data }: { data: any }) => {
    const [index, setIndex] = useState(0);

    useEffect(() => {
        const timer = setInterval(() => {
            setIndex((prev) => (prev + 1) % highlights.length);
        }, 3000);
        return () => clearInterval(timer);
    }, []);

    return (
        <Section>
            <Container>
                <HeroGrid>
                    <Left>
                        <Title
                            initial={{ opacity: 0, x: -50 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.8 }}
                        >
                            Hello I am <span style={{ color: 'var(--primary)' }}>{data.name}</span>
                        </Title>
                        <Subtitle
                            initial={{ opacity: 0, x: -50 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.8, delay: 0.2 }}
                        >
                            A {data.title} with {data.experience_years} years experience. {data.bio}
                        </Subtitle>
                    </Left>
                    <Right>
                        <AnimatePresence mode="wait">
                            <SliderContent
                                key={index}
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                exit={{ opacity: 0, y: -20 }}
                                transition={{ duration: 0.5 }}
                            >
                                <h3>{highlights[index].title}</h3>
                                <p>{highlights[index].text}</p>
                            </SliderContent>
                        </AnimatePresence>
                    </Right>
                </HeroGrid>
            </Container>
        </Section>
    );
};
