'use client';

import React, { useState } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { X } from 'lucide-react';
import { Container, Section } from '../ui/Container';

const CertGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
`;

const CertCard = styled(motion.div)`
  background: white;
  border: 1px solid var(--border);
  border-radius: 1rem;
  overflow: hidden;
  cursor: pointer;
  box-shadow: var(--shadows-sm);
  &:hover {
    box-shadow: var(--shadows-md);
  }
`;

const CertImg = styled.img`
  width: 100%;
  height: 200px;
  object-fit: cover;
`;

const CertInfo = styled.div`
  padding: 1.5rem;
  h4 {
    color: var(--primary);
    margin-bottom: 0.5rem;
  }
  p {
    font-size: 0.9rem;
    color: var(--secondary);
  }
`;

const ModalOverlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
`;

const ModalContent = styled(motion.div)`
  background: white;
  max-width: 800px;
  width: 100%;
  border-radius: 1.5rem;
  overflow: hidden;
  position: relative;
`;

const CloseBtn = styled.button`
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: white;
  background: rgba(0,0,0,0.5);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2001;
`;

export const Certificates = ({ data }: { data: any[] }) => {
    const [selected, setSelected] = useState<any>(null);

    return (
        <Section>
            <Container>
                <h2 style={{ textAlign: 'center', fontSize: '2.5rem', marginBottom: '3rem' }}>Certifications</h2>
                <CertGrid>
                    {data?.map((cert, i) => (
                        <CertCard
                            key={cert.id}
                            whileHover={{ y: -10 }}
                            onClick={() => setSelected(cert)}
                        >
                            <CertImg src={cert.image} alt={cert.title} />
                            <CertInfo>
                                <h4>{cert.title}</h4>
                                <p>{cert.issuer} • {cert.date}</p>
                            </CertInfo>
                        </CertCard>
                    ))}
                </CertGrid>
            </Container>

            <AnimatePresence>
                {selected && (
                    <ModalOverlay
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={() => setSelected(null)}
                    >
                        <CloseBtn onClick={() => setSelected(null)}><X size={24} /></CloseBtn>
                        <ModalContent
                            initial={{ scale: 0.9, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            exit={{ scale: 0.9, opacity: 0 }}
                            onClick={(e) => e.stopPropagation()}
                        >
                            <img src={selected.image} alt={selected.title} style={{ width: '100%', height: 'auto' }} />
                            <div style={{ padding: '2rem' }}>
                                <h3>{selected.title}</h3>
                                <p style={{ color: 'var(--secondary)', marginTop: '0.5rem' }}>Issued by {selected.issuer} in {selected.date}</p>
                            </div>
                        </ModalContent>
                    </ModalOverlay>
                )}
            </AnimatePresence>
        </Section>
    );
};
